	module.exports = {"data":{"wordpressPost":{"title":"How to create salon style hair at home","content":"<p>Having a professional stylist whip your hair into shape is a true pleasure. But carving out the time (and the cash) means that for most of us salon visits are a rare luxury. The great news is that with just a little bit of effort on your part, you can create salon style hair in the privacy of your own home and spend very little money while you’re at it.</p>\n<p><strong>Curl Up</strong></p>\n<p>Take a round barrel heated tong and wrap a two-to-three-inch section of hair around it, says stylist Anthony Nader of RAW Hair Salon. Remove the tong, press the curl flat and slide in a large pin so that it holds its shape.</p>\n<p>Do this with about eight sections of hair focusing on the hair that frames your face. Allow drying before removing the pins to reveal sexy loose curls.</p>\n<p>VS Sassoon Big Hair Soft Curls Styler, $42.95. vssassoon.com.au</p>\n<p><strong>Kill the Frizz</strong></p>\n<p>Professional hair means zero frizz. Stylists achieve this with straightening irons which they glide through hair from the absolute root to the very tip.</p>\n<p>“For a professional finish straighten only small sections at a time and get as close to the scalp as you can,” says Anthony.</p>\n<p>Remington Keratin and Argan Oil Nourish Straightener, $69.95. remington-products.com.au</p>\n<p>Essano Argan Oil Hair Recovery Serum, $15.99. essano.com.au</p>\n<p><strong>Pump Up the Volume</strong></p>\n<p>True glamour means big bouncy beauty queen hair. To create this effect at home, blow dry as you would normally. Flip your hair upside down. Take a texturising powder or dry shampoo and apply at the roots. Massage in, flip your head back and voila &#8211; instant VA Va Voom.</p>\n<p>Batiste Marrakech Dry Shampoo, $9.95. batistehair.com.au</p>\n<p>Make Big for Immense Volume Dust, $21.95. hairhousewarehouse.com.au</p>\n<p><strong>Take Care</strong></p>\n<p>Salon-worthy hair means regular hydrating treatments that reverse the damage. Use a mask weekly and leave it on for at least three minutes before washing out. Want to up the impact? Rinse with cold water.</p>\n<p>“It takes courage but cold water will close the cuticle meaning hair will reflect light better and look super shiny,” says Anthony.</p>\n<p>Bambi &amp;amp; Sammi No. 1 Masque, $16.95. bambiandsammi.com</p>\n<p>Pantene 3 Minute Miracle Repair &amp;amp; Protect Conditioner, $7.99.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/02/27130848/beauty2.jpg"},"categories":[{"slug":"hair","name":"Hair"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"ab3184cf-27f4-5c4a-890d-a1e658876a29"}}

